.d_rowFooter {
  padding-bottom: 24px; }

.d_rowGrey {
  background-color: #e5e5e5; }

.c_Footer {
  z-index: 10;
  background: #102532;
  position: relative;
  padding: 0 64px;
  margin-top: 48px;
  font-size: 13px;
  font-weight: 300;
  text-align: left;
  line-height: 1; }

@media only screen and (max-width: 768px) {
    .c_Footer {
      padding: 0; } }

.c_Footer .logo_own {
    width: 165px; }

.c_Footer .footer-box {
    max-width: 1200px;
    margin: 0 auto; }

.c_Footer .footer-vertical-section {
    margin-top: 8px; }

.c_Footer .footer-vertical-section .title {
      margin-bottom: 16px; }

.c_Footer .footer-vertical-section .ant-avatar {
      background-color: transparent; }

.c_Footer .social-icons > a {
    color: #fff !important;
    display: inline-block;
    padding: 0 24px 24px 0; }

.c_Footer .join {
    padding-left: 24px; }

.c_Footer .footer-color-dark {
    margin-top: 8px;
    color: #5E6C86; }

.c_Footer .footer-color-dark > a {
      color: #5E6C86; }

.c_Footer .contact-container .footer-color-dark > a {
    text-decoration: underline; }
